* {
  margin: 0;
  padding: 0;
}

html,
body,
#root {
  width: 100%;
  height: 100%;
}

::-webkit-scrollbar {
  width: 0px;
  height: 0px;
}

::-webkit-scrollbar-track {
  background: transparent;
}

::-webkit-scrollbar-thumb {
  background: transparent;
}

li {
  list-style: inside;
}

.member-box {
  background: #ffff00
}

.SplitPane {
  width: 100%;
  height: 100%;
}

.SplitPane-left {
  float: left;
  width: 30%;
  height: 100%;
}

.SplitPane-right {
  float: left;
  width: 70%;
  height: 100%;
}

.Contacts {
  width: 100%;
  height: 100%;
  background: lightblue;
}

.Chat {
  width: 100%;
  height: 100%;
  background: pink;
}

.header {
  width: 100%;
  height: 50px;
  color: #ffffff;
  background: #20232a;
}

.FancyBorder {
  padding: 10px 10px;
  border: 10px solid;
}

.FancyBorder-blue {
  border-color: blue;
}

.Dialog-title {
  margin: 0;
  font-family: sans-serif;
}

.Dialog-message {
  font-size: larger;
}

.star {
  cursor: pointer;
  height: 25px;
  width: 25px;
  margin: 2px;
  float: left;
  background: grey;
  clip-path: polygon( 50% 0%, 63% 38%, 100% 38%, 69% 59%, 82% 100%, 50% 75%, 18% 100%, 31% 59%, 0% 38%, 37% 38%);
}

.star.selected {
  background: red;
}

.color-list {
  width: 100%;
  padding: 15px;
  overflow: hidden;
}

.color-list .color {
  float: left;
  width: 300px;
  min-height: 120px;
  margin: 0 20px 20px 0;
}

.main {
  width: 1920px;
  height: 1080px;
  padding: 20px 30px;
  overflow: hidden;
  background: url(../img/bj.jpg) no-repeat center center;
  background-size: cover;
}